<template>
	<div class="card">
		<h2>My plekollections</h2>
        <div v-if="this.$store.state.userData.my_plekollections.length==0">
            <button class="btn btn4" @click="create()">
			Create
		    </button>
        </div>
		<div
            v-else
			class="list"
			v-for="(pc, i) in this.$store.state.userData.my_plekollections"
		>
			<div @click="selectPlekollection(i)">
				<div
					v-if="
						this.$store.state.selectedPlekollection &&
						i == selected_i &&
						pc.name == this.$store.state.selectedPlekollection.name
					"
					class="card"
				>
					<h3>{{ this.$store.state.selectedPlekollection.name }}</h3>
					<button
						v-if="
							this.$store.state.featureCollections
								.map((x) => x.props.name)
								.includes(pc.name)
						"
						@click="removeFromMap(pc)"
						class="btn3"
					>
						x
					</button>
					<button  v-else @click="map()" class="btn btn2">Map</button>
					<button @click="edit()" class="btn btn2">Edit</button>

					<div
						class="list"
						v-for="p in this.$store.state.selectedPlekollection
							.plekjes"
					>
						{{ p.name }}
					</div>
				</div>
				<div v-else>
					{{ pc.name }}
					<button
						v-if="
							this.$store.state.featureCollections
								.map((x) => x.props.name)
								.includes(pc.name)
						"
						@click="removeFromMap(pc)"
						class="btn3"
					>
						x
					</button>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
var api = require("@/utils/api");
export default {
	data() {
		return {
			selected_i: null,
		};
	},
	methods: {
		selectPlekollection(i) {
			this.selected_i = i;
			let id = this.$store.state.userData.my_plekollections[i]._id;
			api.setSelectedPlekollection(id,()=>{});
		},
        create(){
            this.$store.state.creatingPlekollection = true;
            this.$router.replace("/");
        },
		map() {
			api.setFeatureCollectionToMap(() => {
				this.$router.replace("/");
			});
		},
		edit() {
            this.$store.state.editingPlekollection = true;
            this.$store.state.featureCollections = [];
            api.setFeatureCollectionToEdit()
        },
		removeFromMap(pc) {
            this.$store.commit("filterFeatureCollections", pc.name);
        },
	},
};
</script>

<style>
.btn2 {
	margin-right: 0px;
	margin-left: 5px;
	margin-top: 5px;
	float: right;
}
.btn3 {
	float: right;
	border: none;
	margin: 5px;
    margin-top:0;
	font-size: 25px;
	font-weight: bold;
	color: #dc3545;
	background-color: inherit;
}

.btn4{
    margin-bottom: 10px;
    margin-left: 50%;
    transform: translate(-50%, 0%);
}
</style>