<template>
	<div class="card" style="text-align: center" @click="goAbout()">
		<h1>Plekjes</h1>
		<p>V0.7 pre-alpha</p>
	</div>
    <div v-if="about">
        <About></About>
    </div>
    <div v-else-if="this.$store.state.userIsAuthenticated">
        <UserData v-if="this.$store.state.userEmail"></UserData>
        <div v-else>
            <p>Loading profile...</p>
        </div>
    </div>

    <div v-else>
        <button class="btn" @click="this.$store.dispatch('auth0Login')">Log in</button>
    </div>
  
</template>

<script>
import UserData from "../components/UserData.vue";
import About from "../components/About.vue";

export default {
    components: {
        UserData,
        About
    },
    data(){
        return{
            about:false
        }
    },
    methods:{
        goAbout(){
            this.about=!this.about;
            console.log(this.about)
        }
    },
    beforeCreate() {
		if (!this.$store.state.userEmail) {
			this.$store.dispatch("auth0getUserEmail");
		}
	},
}
</script>

