<template>
	<br />
	<span>Choose a Username: </span>
	<br />
	<input v-model="userName" />
	<br />
	<button class="btn" @click="checkUserNameAvailability()">Save</button>

	<div v-if="!userNameAvailable" id="warning">
		<br />
		This username is unavailable
	</div>
</template>

<script>
var api = require("@/utils/api");
export default {
	data() {
		return {
			userName: "",
			userNameAvailable: true,
		};
	},
	methods: {
		checkUserNameAvailability() {
            api.checkUserNameAvailability(this.userName,(available) => {
                this.userNameAvailable = available;
			})
        },
	},
};
</script>

<style scoped>
span {
	font-size: 21px;
	text-align: left;
}

input {
	padding: 5px 10px;
	font-size: 21px;
	margin: 10px;
}
#warning {
	color: #dc3545;
}
</style>