<template>
	<div class="card" v-if="this.$store.state.featureCollectionToEdit">
		<h2>Editing {{this.$store.state.featureCollectionToEdit.props.name}}</h2>
		<div
			class="list"
			v-for="(f, i) in this.$store.state.featureCollectionToEdit.features"
		>
			{{ f.properties.name }}
			<button
				
				@click="removeFeature(f)"
				class="btn3"
			>
				-
			</button>
		</div>
	</div>
</template>

<script>
export default {
    methods:{
        removeFeature(feature){
            if (
				confirm(
					`Delete "${feature.properties.name}" from "${this.$store.state.featureCollectionToEdit.props.name}"?`
				)
			) {
				this.$store.state.featureCollectionToEdit.features =
					this.$store.state.featureCollectionToEdit.features.filter(
						(f) => {return f.properties.name != feature.properties.name}
					);
				//this.$store.commit("reloadMap");
			} else {
				console.log("canceled");
			}
        }
    }
};
</script>

